import React, { useRef, useState, useEffect } from "react";
import { Col, Row, Container, Button } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";

export default function ResumenServicio(props){
    const [modal, setModal] = useState(false);
    const [error, setError] = useState(0);
    const [click, setClick] = useState(false);
    const [paquetes, setPaquetes] = useState({Nombre: "", Precio: ""})
    const [paqAdicionales, setPaqAdicionales] = useState(); //Se añade en caso de que se vuelvan a agregar los adicionales
    const [datosCoche, setDatosCoche] = useState({kilometros: 0, modelo: ""})

    const refForm = useRef();

    useEffect(() => {
        if (typeof window !== 'undefined') {
            //console.log('we are running on the client', localStorage.getItem("RutaNavegacion"));
            setPaquetes({
                ...paquetes,
                Nombre: localStorage.getItem("paqueteClase"),
                Precio: localStorage.getItem("paquetePrecio"),
            })

            if(localStorage.getItem("RutaNavegacion") === "sin-cita"){
                var datosC = JSON.parse(localStorage.getItem("informacion"));
                //console.log("Tengo los siguientes sin-cita: ", datosC);
                setDatosCoche({
                    ...datosCoche,
                    modelo: datosC.data.model,
                    kilometros: localStorage.getItem("Kilo")
                })
            } else if(localStorage.getItem("RutaNavegacion") === "orden-reparacion") {
                var datosC = JSON.parse(localStorage.getItem("informacion"));
                console.log("Tengo los siguientes datos: ", datosC);
                setDatosCoche({
                    ...datosCoche,
                    modelo: datosC[0].car.model,
                    kilometros: datosC[0].car.km
                })
            } else if(localStorage.getItem("RutaNavegacion") === "nuevo-usuario") {
                var datosC = JSON.parse(localStorage.getItem("informacion"));
                console.log("Tengo los siguientes datos: ", datosC);
                setDatosCoche({
                    ...datosCoche,
                    modelo: datosC.data.Modelo,
                    kilometros: datosC.data.Kilometraje
                })
            }

            //var datos = JSON.parse(localStorage.getItem("adicionales")); Paquetes adicionales
            //setPaqAdicionales(datos.paquetesAdicionales || "");


        } else {
            //console.log('we are running on the server');
        }
    }, []);

    const componenteEditar = () => {
        return(
            <span class="f">
                <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-pencil-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" d="M12.854.146a.5.5 0 0 0-.707 0L10.5 1.793 14.207 5.5l1.647-1.646a.5.5 0 0 0 0-.708l-3-3zm.646 6.061L9.793 2.5 3.293 9H3.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.207l6.5-6.5zm-7.468 7.468A.5.5 0 0 1 6 13.5V13h-.5a.5.5 0 0 1-.5-.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.5-.5V10h-.5a.499.499 0 0 1-.175-.032l-.179.178a.5.5 0 0 0-.11.168l-2 5a.5.5 0 0 0 .65.65l5-2a.5.5 0 0 0 .168-.11l.178-.178z"/>
                </svg>
            </span>  
        )
    }

    const sumatoria = paqAdicionales?.map(item => item.precio).reduce((prev, curr) => prev + curr, 0);

    //const sum = Number(sumatoria) + Number(paquetes.Precio);

    function continuar(){
        //console.log("Pulsado jiji");
        navigate("/postpago-tarjeta", {
            state: { navigate: "resumen-servicio" },
        });

        //localStorage.setItem("totalPagar", sumatoria);
    }

    return(
        <Layout backUrl={"/comentarios-paquetes"}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>}
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">Resumen de servicios adicionales </h1></Col>
                        </Row>
                        <Container  style={{maxWidth: '70%'}}>
                            <Row>
                                <Col lg={12} style={{paddingBottom: 10}}><strong>Información del Vehículo</strong></Col>
                                <Col lg={8}>Modelo</Col><Col lg={4}> {datosCoche.modelo} </Col>
                                <Col lg={8}>Kilómetros</Col><Col lg={4}> {datosCoche.kilometros} kilómetros</Col>
                                <Col lg={12} style={{borderTop: '1px solid rgb(235,235,235)', paddingBottom: 10, marginTop: 10}}></Col>

                                <Col lg={8} style={{paddingBottom: 10}}><strong>Paquetes</strong></Col><Col lg={4} onClick={() => navigate("/realizar-servicio")}>Editar {componenteEditar()}</Col>
                                <Col lg={8}>{paquetes.Nombre}</Col><Col lg={4}>$ {paquetes.Precio} pesos</Col>
                                
                                { paqAdicionales?.length > 0 ? <Col lg={12} style={{marginTop: 10, paddingBottom: 10}}><strong>Paquetes adicionales</strong></Col> : null }
                                {
                                    paqAdicionales?.map((obj, index) => {
                                        return(
                                            <>
                                                <Col lg={8}>{obj.servicio}</Col><Col lg={4}>$ {obj.precio} pesos</Col>
                                            </>
                                        )
                                    })
                                }
                                <Col lg={12} style={{borderTop: '1px solid rgb(235,235,235)', paddingBottom: 10, marginTop: 10}}></Col>
                                <Col lg={8}><h5><strong>Total a pagar</strong></h5></Col><Col lg={4} className="texto-azul"><h5><strong>$ {paquetes.Precio} pesos</strong></h5></Col>
                            </Row>
                        </Container>
                    </div>
                </Row>
                <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}